.ic_msp {
    width: 40px;
    height: 60px;
    fill: black;
}

.msp {
    text-align: center;
    padding: 20px;
    border: 3px solid $black;
    display: inline-block;
    font-size: 30px;
    color: $black;
    text-decoration: none;
    transition: all .3s;
    background: #fff;
    margin: 0 100px;

    &__info {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &:hover {
        .ic_msp {
            fill: #fff;

        }

        color: #fff;
        background: $black;
    }

}

@media only screen and (max-width : 730px) {
    .msp {
        margin: 0 0;
    }

}

@media only screen and (max-width : 550px) {
    .msp {
        font-size: 17px;
    }
}
