.content_wrp {
    display: flex;

    hr {
        border: 2px solid $black;
    }
}

.content_blocks {
    display: flex;
    flex-basis: 70%;

}

.content_buttons {
    flex-basis: 30%;
}

.blocks_column {
    display: flex;
    flex-direction: column;
}

.block {
    padding: 10px;
    border: 3px solid $black;
    margin: 6px;
    height: 100%;
}

.block_top block_w {
    display: flex;
    flex-direction: column;
}

.block_title {
    font-size: 29px;
    text-transform: uppercase;
    margin-bottom: 12px;
    font-weight: 600;
}

.block_subtitle {
    font-size: 19px;
}

.block_subtitle--stat {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 10px;
}

.block_info--title {
    margin-bottom: 30px;
}



.block_wrp {
    display: flex;
    justify-content: space-between;
}

.block_info {
    text-align: center;
    margin-bottom: 10px;
    width: 100%;
}

.block_info--title {
    color: #BA205A;
    font-size: 42px;
    margin-bottom: 5px;
    font-weight: 600;
}

.block_info--title-mb {
    margin-bottom: 40px;
}

.block_info--subtitle {
    font-weight: 600;
}

.block_subtitle--margin-bot {
    margin-bottom: 40px;
}

.title-mobile {
    display: none;
}

@media only screen and (max-width : 810px) {
    .content_wrp {
        flex-wrap: wrap;
    }

    .content_buttons {
        display: flex;
        flex-basis: 100%;
        align-items: flex-start;
        justify-content: space-between;
    }

    .content_blocks {
        display: flex;
        flex-basis: 100%;

    }

    .block_title {
        font-size: 20px;

    }

    .block_subtitle {
        font-size: 17px;
    }

    .block_info--title {
        font-size: 36px;

    }



}

@media only screen and (max-width : 576px) {
    .title-mobile {
        display: block;
        font-size: 17px;
        font-weight: 700;
        margin-bottom: 15px;
        text-align: center;
    }

    .content_blocks {
        flex-wrap: wrap;
    }

    .blocks_column {
        flex-basis: 100%;
    }

    .radio-tile-label-five {
        font-size: 18px !important;
        padding-left: 10px;
        padding-right: 10px;

    }

    .block_subtitle--margin-bot {
        margin-bottom: 10px;
    }

    .block_info--title-mb {
        margin-bottom: 0px;
    }

    .block {
        text-align: center;
    }

}
