#ic_gov {

    --color-wblue: transparent;
    --color-fiolett: transparent;
    --color-blue: #fff;
    --color-black: #000;

}

#ic_calc {

    --color-red: #fff;
    --color-blue: #fff;

}

#ic_cran {
    --color-yellow: transparent;
    --color-orange: transparent;

}

#ic_ambar {

    --color-dbrown: #201600;
    --color-lbrown: transparent;
    --color-brown: transparent;


}


#ic_sh {

    --color-green: transparent;
    --color-white: #000;
    --color-blue: transparent;


}

#ic_hotel {

    --color-green: #fff;
    --color-yellow: #fff;
}

#ic_plant {

    --color-blue: #fff;
    --color-brown: #fff;
    --color-transparent: #fff;
    --color-stroke: #fff;
    --color-gray: #fff;
}


#ic_car {

    --color-blue: transparent;
    --color-brown: transparent;
    --color-white: transparent;
    --color-stroke: transparent;

}

#ic_vopr {
    fill: $black;
}

#ic_crest {
    fill: "#201600";
}

#ic_envelop {
    fill: #000105;
}

.input-container {
    &:hover #ic_plant {
        --color-blue: #6CA9DF;
        --color-brown: #A04944;
        --color-transparent: #fff;
        --color-stroke: #000;
        --color-gray: #AFAEAE;

    }

    &:hover #ic_car {
        --color-blue: #97B7E3;
        --color-brown: #D4584D;
        --color-white: #fff;
        --color-stroke: #000;

    }

    &:hover #ic_hotel {
        --color-green: #00A99D;
        --color-yellow: #FFF243;

    }

    &:hover #ic_gov {
        --color-wblue: #29ABE2;
        --color-fiolett: #2E3192;
        --color-blue: #0071BC;
        --color-black: #fff
    }

    &:hover #ic_sh {
        --color-green: #8CC63F;
        --color-white: #fff;
        --color-blue: #29ABE2;
    }

    &:hover #ic_vopr {
        fill: #ED1C24;
    }

    &:hover #ic_crest {
        fill: #ED1C24;
    }

    &:hover #ic_ambar {
        --color-dbrown: #201600;
        --color-lbrown: #C7B299;
        --color-brown: #736357;

    }

    &:hover #ic_cran {
        --color-yellow: #FADB49;
        --color-orange: #D4584D;

    }

    &:hover #ic_calc {
        --color-red: #BA205A;
        --color-blue: #29ABE2;

    }

    &:hover #ic_envelop {
        fill: #29ABE2;

    }

}

.input-container--fulwidth {
    width: 100%;


}

.radio-tile-group {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-basis: 20%;
    margin: 0 5px;


    .input-container {

        .radio-button {
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            margin: 0;
            cursor: pointer;

        }

        .radio-tile {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
        }

        .icon {

            margin-bottom: 27px;
        }

        .ic_colored {
            width: 100px;
            height: 100px;
        }



        .radio-tile-label {
            color: $black;
            font-size: 18px;
            line-height: 1.3;
            text-align: center;
        }


        .radio-button:checked + .radio-tile {
            #ic_plant {
                --color-blue: #6CA9DF;
                --color-brown: #A04944;
                --color-stroke: #000;
                --color-gray: #AFAEAE;
            }

            #ic_car {
                --color-blue: #97B7E3;
                --color-brown: #D4584D;
                --color-white: #fff;
                --color-stroke: #000;
            }

            #ic_hotel {
                --color-green: #00A99D;
                --color-yellow: #FFF243;
            }

            #ic_gov {
                --color-wblue: #29ABE2;
                --color-fiolett: #2E3192;
                --color-blue: #0071BC;
                --color-black: #fff;
            }

            #ic_sh {
                --color-green: #8CC63F;
                --color-white: #fff;
                --color-blue: #29ABE2;
            }

            #ic_vopr {
                fill: #ED1C24;
            }

            #ic_ambar {
                --color-dbrown: #201600;
                --color-lbrown: #C7B299;
                --color-brown: #736357;

            }

            #ic_cran {
                --color-yellow: #FADB49;
                --color-orange: #D4584D;

            }

            #ic_crest {
                fill: #ED1C24;
            }
        }
    }
}

.icon {
    z-index: 10;
}

.radio-tile-label-five {
    border: 3px solid $black;
    padding: 30px;
    width: 100%;
    font-size: 29px !important;
    margin-top: -50px;
    z-index: 0;
}

.container--plant {
    position: relative;
}

.tooltip-hidden {
    transition: all .3s;
    position: absolute;
    z-index: 120;
    bottom: -200px;
    left: 0;
    background-color: #fff;
    width: 743px;
    height: 140px;
    padding: 40px 40px;
    border: 3px solid $black;

    &::after {
        content: '';
        width: 80px;
        height: 80px;
        background: url('../img/tooltip.svg');
        background-repeat: no-repeat;
        z-index: 140;
        display: block;
        position: absolute;
        left: 40px;
        bottom: 102px;

    }

}

.tooltip-hidden--large {
    padding: 20px 20px;
    height: 180px;
    bottom: -200px;
    left: 60px;

    &::after {
        left: 80px;
        bottom: 142px;
    }

    .tooltip-info {
        width: 100%;
        margin-bottom: 20px;


    }

    .tooltip-question {
        flex-basis: 80%;
        margin-right: 40px;
    }

    .tooltip-inputs {
        flex-basis: 20%;

    }
}



.tooltip-info {
    display: flex;
    width: 90%;
    align-items: center;
    background: #fff;
    justify-content: space-between;
}

.tooltip-question {
    font-size: 21px;
    line-height: 1.3;
    flex-basis: 50%;

}

.tooltip-inputs {
    display: flex;
    flex-basis: 50%;
    justify-content: flex-end;

}

.tooltip-inputs--center {
    justify-content: center;
}



@media only screen and (max-width : 1275px) {
    .radio-tile-group {
        flex-basis: 30%;
        margin-bottom: 20px;
    }

    .tooltip-hidden {
        position: absolute;
        z-index: 120;
        bottom: -200px;
        left: 0;
        background-color: #fff;

        height: 140px;
        padding: 40px 40px;
        border: 3px solid $black;

        &::after {
            content: '';
            width: 80px;
            height: 80px;
            background: url('../img/tooltip.svg');
            background-repeat: no-repeat;
            z-index: 140;
            display: block;
            position: absolute;
            left: 40px;
            bottom: 102px;

        }

    }

    .tooltip-hidden--large {
        padding: 20px 20px;
        height: 180px;
        bottom: -230px;

        &::after {
            left: 80px;
            bottom: 142px;
        }

        .tooltip-info {
            width: 100%;
            margin-bottom: 20px;


        }

        .tooltip-question {
            flex-basis: 80%;
            margin-right: 40px;
        }

        .tooltip-inputs {
            flex-basis: 20%;

        }
    }



}



@media only screen and (max-width : 758px) {
    .tooltip-info {
        display: flex;
        justify-content: space-between;
    }

    .tooltip-hidden {
        bottom: -180px;
        width: auto;
        left: 50px;

    }

    .tooltip-question {
        font-size: 16px;
        line-height: 1.3;
        flex-basis: 50%;
        margin-right: 20px;
    }

    .tooltip-hidden--large {
        padding: 20px 20px;
        height: 390px;
        bottom: -450px;

        &::after {
            left: 80px;
            bottom: 352px;
        }



        .tooltip-info {
            flex-wrap: wrap;
            justify-content: center;
            text-align: center;
        }

        .tooltip-question {
            margin-bottom: 20px;

        }

    }
}

@media only screen and (max-width : 550px) {
    .radio-tile-group {
        flex-basis: 40%;

        .input-container {
            .radio-tile-label {
                font-size: 10px;

            }
        }
    }

    .step_wrp--centered .radio-tile-group {
        flex-basis: 100%;


    }




    .tooltip-question {
        margin-bottom: 20px;
    }

    .tooltip-info {
        flex-wrap: wrap;
    }

    .tooltip-hidden {

        bottom: -190px;
        padding: 20px 20px;


    }

    .tooltip-hidden--large {
        padding: 20px 20px;
        height: 320px;
        bottom: -30px;
        left: 0px;

        &::after {
            left: 120px;
            bottom: 282px;
        }
    }

}
