.custom-checkbox {
    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;
}

.checkbox-hidden {
    display: none;
}

.checkbox-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    max-width: 20px;
    flex: 0 0 20px;
    height: 20px;
    border-radius: 2px;
    border: 2px solid $black;
}

.custom-checkbox input:checked + .checkbox-wrap::after {
    transform: scale(1);
    opacity: 1;
}

.custom-checkbox .checkbox-wrap::after {
    content: "";
    display: block;
    width: 12px;
    height: 12px;
    /* background: url(../img/svg-check-ic.svg) no-repeat 0 0;*/
    background: #404D9C;
    background-size: 100%;
    will-change: transform;
    transform: scale(0);
    opacity: 0;
    transition: all 0.1s linear;
}



/*Радио кнопка*/
.input-hidden {
    display: none;
}

.ui-radiobox {
    display: block;
    margin-bottom: 0;
    cursor: pointer;
}

.ui-radiobox_custom {
    font-weight: 300;
    font-size: 18px;
    margin-right: 30px;
}

.ui-radiobox__label {
    display: flex;
    align-items: center;
    position: relative;
    line-height: 1.4;
}

.ui-radiobox__label::before {
    width: 26px;
    flex: 0 0 26px;
    height: 26px;
    margin-right: 10px;
    background: #fff;
    border: 2px solid $black;
}

.ui-radiobox__label::before,
.ui-radiobox__label::after {
    content: "";
    display: block;

}

.ui-radiobox input:checked + .ui-radiobox__label::after {
    transform: scale(1);
}

.ui-radiobox__label::after {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: #404D9C;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}


.ui-radiobox__label--red::after {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: #BA205A;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}


.tooltip-inputs--center {
    margin-bottom: 183px;

    .ui-radiobox_custom {
        font-weight: 300;
        font-size: 58px;
        margin-right: 30px;
    }


    .ui-radiobox__label::before {
        width: 49px;
        flex: 0 0 49px;
        height: 49px;
        margin-right: 10px;
        background: #fff;
        border: 3px solid $black;
    }

    .ui-radiobox__label::after {
        width: 43px;
        height: 43px;
        top: 19px;
        left: 3px;

    }


    .ui-radiobox__label--red::after {
        width: 43px;
        height: 43px;
        top: 19px;
        left: 3px;

    }
}

@media only screen and (max-width : 550px) {
    .tooltip-inputs--center {
        margin-bottom: 183px;

        .ui-radiobox_custom {
            font-weight: 300;
            font-size: 31px;
            margin-right: 30px;
        }


        .ui-radiobox__label::before {
            width: 30px;
            flex: 0 0 30px;
            height: 30px;
            margin-right: 10px;
            background: #fff;
            border: 3px solid $black;
        }

        .ui-radiobox__label::after {
            width: 24px;
            height: 24px;
            top: 10px;
            left: 3px;

        }


        .ui-radiobox__label--red::after {
            width: 24px;
            height: 24px;
            top: 10px;
            left: 3px;

        }
    }


}
