.wrap {
    padding-top: 100px;
    min-width: 320px;
    max-width: 1600px;
    box-sizing: border-box;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;

}

.steps {
    display: flex;
    height: 600px;
    width: 100%;
}



.step {
    display: flex;
    height: 100%;
}


.step-content {
    display: none;
    padding: 25px 20px 25px 20px;
    flex-direction: column;
    background: #fff;
    border: 1px solid #000000;
    margin-left: -2px;

}

.step-content-five {
    padding: 10px;

}

.step-content--active {
    display: block;

}


.step-number {
    background: #000;
    border-right: 1px solid #fff;
    padding: 22px 16px;
    display: flex;
    justify-content: center;
    width: 75px;
    cursor: pointer;


}



.step-title {
    font-size: 34px;
    line-height: 1.3;
    margin-bottom: 64px;
    display: block;
}



.step_wrp {
    display: flex;
    width: 100%;
    align-items: flex-start;

}

.step_wrp--centered {
    justify-content: space-around;
    position: relative;
}


.icon-car {
    margin-left: 25px;
}

.ic_number {
    width: 36px;
    height: 50px;
    fill: #fff;
}

@media only screen and (max-width : 1274px) {
    .step_wrp {
        flex-wrap: wrap;
    }
}

@media only screen and (max-width : 1200px) {
    .step {
        flex-direction: column;

    }

    .step-content {
        position: absolute;
        left: 0;
        top: 190px;
        border: none;
    }

    .step-number--active {
        background: #fff;

        .ic_number {
            fill: $black;
        }
    }

}

@media only screen and (max-width : 550px) {
    .step_wrp {
        justify-content: space-between;

    }

    .step_wrp--centered {
        justify-content: center;
    }

    .step-title {
        font-size: 17px;
        margin-bottom: 20px;

    }

    .step-number {
        width: 64px;


    }

}

.next-prev {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-top: 13px;
}

.ic_next {
    width: 28px;
    height: 32px;
}

.next {
    text-align: end;
}

.arrow {
    display: flex;
    align-items: center;
    color: $black;
    text-decoration: none;

    &:hover {
        opacity: .8;
    }

    span {
        margin: 0px 5px;
        font-size: 15px;
        color: $black;

    }
}

.next-prev--last {
    .next {
        opacity: 0;
    }
}
.next-prev--first {
    .prev {
        opacity: 0;
    }
}

@media only screen and (max-width : 1200px) {
    .next-prev {
        display: none;
    }

}
